import * as React from 'react';


export class Testimonials extends React.Component {

  render() {

    return <div>
      <h2>Student Testimonials</h2>
      <p>Here are some of the many student testimonials that we have received.</p>

      {!this.props.data.testimonials
        ? <div>Loading...</div>
        : this.props.data.testimonials.map(t => <div>
        <blockquote>
          {t.testimonial}
        </blockquote>
        <i>- Student from {t.className}</i>
      </div>)}
    
    </div>;
  }

}


